.weather{
    border-top: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.weather h3{
    text-align: center;
    margin: 2rem;
    font-size: 1.5rem;
}
.weather input{
    padding: 5px 10px;
    border: 1px solid black;
    border-radius: 5px;
    outline: none;
    width: 250px;
    color: crimson;
    font-size: 1rem;
    margin: 1rem;
}
.weather p{
    margin: 0.5rem;
    font-size: 1.3rem;
}
.weather span{
    color: crimson;
}

 button {
    padding:4px 10px;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1.2rem;
    border: none;
    color: white;
    background-color: black;
    cursor: pointer;
    margin-left: 1rem;
  }
   button:hover {
    font-size: 1.18rem;
    border: none;
    color: white;
    background-color: rgb(182, 74, 7);
    transition: 0.2s ease-in-out;
  }

  @media (max-width: 650px){
    .weather p{
        font-size: 1rem;
    }
    .weather input{
        width: 220px;
        color: crimson;
        font-size: 0.9rem;
    }

  }