*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.userDashboard img{
    width: 100px;
    border-radius: 50%;
    
}

.userContainer{
    width: 350px;
    height: 60vh;
    background-color: white;
    border-radius: 12px;
}
.userDashboard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: rgb(229, 118, 118);
    text-align: center;
}
.userDashboard p{
    font-size: 1.3rem;
    color: rgb(229, 118, 118);
    text-align: center;
    margin: 1rem 0;
}
.userDashboard h2{
    margin: 2rem 0;
}
.userDashboard .user{
margin: 2rem;
color: rgb(229, 118, 118);
}