.contactDes{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem;
    background-color: #b64a07;
}
.contactDes p{
    color: white;
    font-size: 1.1rem;
}
.contactDes a{
    text-decoration: none;
    font-size: 1.1rem;
    cursor: pointer;
    padding: 0.5rem 1rem;
    margin: 0 0.5rem;
    border-radius: 13px;
    background-color: white;
    color: black;
}
.contactDesIcon{
    padding: 1rem ;
}
.contactDes a:hover{
    color: white;
    background-color: black;
}

.contactFeedback{
    border: 1px solid black;
    margin: 1rem;
    width: 30rem ;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.feedbackContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #b64a07;
    flex-direction: column;
}
.feedbackContainer p{
    color: white;
    font-size: 1.1rem;
}
.contactForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: antiquewhite;
    width: 100%;
    border-radius: 8px;
    padding: 2rem 0;
}
.contactForm input{
    padding: 0.5rem 0.4rem;
    margin: 10px;
    width: 25rem;
    letter-spacing: 2px;
    border-bottom: 1px solid black !important;
    border: none;
    outline: none;
    color: #b64a07;
}
.contactForm input[type="Submit"]{
    
    background-color:#b64a07 ;
    color: white;
    border-radius: 13px;
    font-size: 1.1rem;
    border: none;
    cursor: pointer;
    width: 5rem;
}
.contactForm input[type="Submit"]:hover{
    background-color: black;
    color: white; 
    transform: scale(0.9);
    transition: 0.2s ease-in-out;
    
}
.contactForm input[type="placeholder"]{
  margin-left: 1rem;
}


.contactForm textarea{
    padding: 0.5rem;
    width: 300px;
    letter-spacing: 1.5px;
    color: #b64a07;
}


.placesDes{
padding: 4rem 3rem;
}

.aboutDes{
    padding: 4rem 3rem;
}

@media (max-width:500px){
    
    .contactFeedback{
        width: 23rem ;
    }
    .contactForm input{
        padding: 0.5rem 0.4rem;
        margin: 10px;
        width: 19rem;
        letter-spacing: 2px;
        border-bottom: 1px solid black !important;
        border: none;
        outline: none;
        color: #b64a07;
    }
}


