.navbarItems {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
  border-radius: 13px;
  width: 95%;
  height: 80px;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translate(-50%);
  z-index: 9999;
  background-color: white;
}
.userIcon img{
  padding: 3px 2px;
  border-radius: 50%;
  cursor: pointer;
  width: 30px;
}
.account  p{
  color: #b64a07;
  font-size: 13px;
}
.link{
  text-decoration: none;
}


.navbarLogo a{
  text-decoration: none;
  color: black;
  font-family: 'Lobster Two', cursive;
  font-size: 2rem;
  cursor: pointer;
}
.navbarLogo a:hover{
  color: #b64a07;
  transition: 0.2s;
}
.navbarMenu {
  display: grid;
  grid-template-columns: repeat(6, auto);
  gap: 10px;
  list-style: none;
  align-items: center;
}
.menuList {
  text-decoration: none;
  color: black;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0.7rem 1rem;
  white-space: nowrap;
}
.menuList i {
  margin-right: 8px;
}

.menuList:hover {
  background-color: #b64a07;
  border-radius: 5px;
  color: white;
  transition: 0.2s ease-in-out;
}

.SignUpButton {
  display: none;
}

.auth button {
  padding: 0.5rem 1rem;
  white-space: nowrap;
  border-radius: 0.3rem;
  font-size: 1.2rem;
  border: none;
  color: white;
  background-color: black;
  cursor: pointer;
}
.auth button:hover {
  font-size: 1.18rem;
  border: none;
  color: white;
  background-color: rgb(182, 74, 7);
  transition: 0.2s ease-in-out;
}
.menuIcons{
    display: none;
}
@media (max-width: 850px) {
    .menuIcons{
        display: block;
        cursor: pointer;
    }
    .menuIcons i{
        font-size: 1.2rem;
        color: black;
    }
  .navbarItems {
    z-index: 1;
  }

  .navbarMenu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: white;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: -100%;
    opacity: 0;
    align-items: stretch;
    text-align: center;
    padding: 80px 0 30px 0;
    margin: 0;
    transition: 0.3s ease-in-out;
    border-radius: 13px;
  }

  .navbarMenu.active {
    left:0%;
    opacity: 1;
    z-index: -1;
    transition: 0.3s ease-in-out;
  }

  .menuList {
    display: block;
    width: 100%;
    padding: 2rem 0;
  }
  .menuList:hover {
    background-color: rgb(182, 74, 7);
    transition: all 0.2s ease-in-out;
  }
  .auth button {
    display: none;
  }
  .SignUpButton {
    display: block;
    text-decoration: none;
    color: white;
    font-size: 1.3rem;
    font-weight: 700;
    background-color: rgb(182, 74, 7);
    padding: 1.5rem;
    margin: auto;
    width: 80%;
    border-radius: 13px;
    letter-spacing: 2px;
  }
  .SignUpButton:hover{
    transition: 0.2s ease-in-out;
    font-size: 1.35rem;
    background-color: black;
    color: white;
  }
}
