.locations{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 4rem 5rem;
}
.locations h1{
    font-size: 3rem;
    white-space: nowrap;
}

.locations h2{
    font-size: 1.5rem;
    white-space: nowrap;
}
.firstDes{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}
.firstDesReverse{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}
.DesText{
width: 45%;
text-align: start;
font-size: 1.1rem;
}
.DesText h2{
    padding-bottom: 1rem;
}

.image{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99 ;
}
.image img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: -1px 1px 62px 0px rgba(0, 0, 0, 0.25);
}
.image img:nth-child(2){
    position: absolute;
    top: -10%;
    right: 0;
}

@media(max-width:850px) {
    .locations{
        margin: 4rem 2rem;
    }
    .firstDes{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: space-between;
        margin-top: 2.5rem;
    }
    .firstDesReverse{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: space-between;
        margin-top: 2.5rem;
    }
    .DesText{
        width: 100%;
        text-align: start;
        font-size: 1.1rem;
        }
        .image{
            width: 80%;
            margin: 1.8rem 0;
        }
        .image img{
            width: 48%;
            height: 200px;
        }
}

@media (max-width:550px){
    .firstDesReverse .img{
        width: 230px;
    }
}