.Destination{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 3rem;
    
}
.Destination h2{
    margin: 1rem;
}
.Destination h3{
    margin: 1rem;
}
.Destination h3 a{
    margin: 1rem;
    cursor: pointer;
    color: black;
    text-decoration: none;
    border-bottom: 2px solid black;
}
.Destination h3 a i{
    margin-left: 1rem;
}
.Destination h3 a:hover{
    color: #b64a07;
    transition: 0.2s;
}
.Destination img{
    width: 350px;
    border-radius: 13px;
}
.Destination img:hover{
    scale: 0.95;
    transition: 0.2s ease-in-out;
}
.desText{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem;
}
.desText p{
margin: 1rem;
}

@media (max-width:550px){
    .Destination{
        margin: 1rem;
        
    }
    .Destination img{
        width: 300px;
    }
    .desText{
        display: flex;
        flex-direction: column;
        margin-top: 1rem;
    }
    .desText p{
        margin-top: 1rem;
        }
}
.placesDes h1{
    text-align: center;
}