.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}

.hero img{
    width: 100%;
    height: 100%;
    object-fit: cover;  
}
.heroAbout img{
    width: 100%;
    height: 100%;
    object-fit: cover;  
}
.heroText{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    text-align: center;
}
.heroText h1{
    font-size: 3rem;
    font-weight: 800;
    letter-spacing: 2px;
    background: white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.heroText p{
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 2px;
    color: white;
    padding: 0.5rem 0 2rem 0;
}
.heroText .HomeBtn{
    text-decoration: none;
    background-color: white;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: black;
}
.heroText .HomeBtn:hover{
    background-color: #b64a07;
    color: white;
    transition: 0.2s;
}

@media screen and (max-width:560px){
    .heroText h1{
        padding: 10px 30px;
        
    }
    .heroText p{
        font-size: 1.1rem;
    }
    .heroText .HomeBtn{
        padding: 0.6rem 1.1rem;
        font-size: 1rem;
        letter-spacing: 1.5px;
    }

}

.heroAbout{
    width: 100%;
    height: 70vh;
    position: relative;
}

.heroImages{
    width: 100%;
    height: 70vh;
    position: relative;
}

